<template>
  <div>
    <!--<div class="panel-title">添加价格调整规则</div>-->
    <div class="info_slide">
      <section class="info">
        <div class="right">
          <div class="rightItem">
            <div class="item_item">
              <div class="item-title">
                <strong>名称</strong>
                <span class="tipMsg">{{msgError.goodsName}}</span>
              </div>
              <input type="text" v-model.trim="goodsType" placeholder="例如:冬季甩卖" @blur="change(goodsType,'goodsName')" ref="goodsName" @input="input('goodsName')" :disabled="status !==0" />
            </div>
          </div>
          <div class="rightItem">
            <div class="item_item">
              <div class="item-title">
                <strong>开始日期</strong>
                <span class="tipMsg">{{msgError.startDateMsg}}</span>
              </div>
              <DatePicker type="date" placeholder="年/月/日" :options="options" style="width:300px;" size="large" :value="dateStart" @on-change="dateStartClick" @on-open-change="startOpen" :disabled="status !==0"></DatePicker>
            </div>
            <div class="item_item">
              <div class="item-title">
                <strong>结束日期</strong>
                <span class="tipMsg">{{msgError.endDateMsg}}</span>
              </div>
              <DatePicker type="date" placeholder="年/月/日" :options="options" style="width:300px" size="large" :value="dateEnd" @on-change="dateEndClick" @on-open-change="endOpen" :disabled="status !==0"></DatePicker>
            </div>
          </div>
          <div class="info">
            <Checkbox v-model="content_show" false-value="1" true-value="2"><span style="font-size: 0.14rem">将规则仅适用于此期间的每周六、周日</span></Checkbox>
          </div>
          <div class="rightItem" v-if="status === 0">
            <div class="item_item">
              <div class="item-title">
                <strong>规则</strong>
                <span class="tipMsg">{{msgError.ruleMsg}}</span>
              </div>
              <div class="input-box">
                <Select v-model="rulesValue" style="width:300px;padding: 0" size="large" @on-change="ruleChange">
                  <Option v-for="item in ruleList" :value="item.value" :key="item.value">{{item.label}}</Option>
                </Select>
              </div>
            </div>
            <div class="item_item">
              <div class="item-title">
                <strong></strong>
                <span class="tipMsg">{{msgError.numberMsg}}</span>
              </div>
              <div class="input-box">
                <input v-model.trim="numberValue" type="text" @input="input('numberMsg')" @blur="change(numberValue,'numberMsg')" ref="numberMsg" @focus="numberFocus" />
              </div>
            </div>
          </div>
          <div class="rightItem">
            <div class="item_item">
              <div class="item-title" v-if="status ===0">
                <strong>适用于</strong>
                <span class="tipMsg">{{msgError.applyMsg}}</span>
              </div>
              <Select v-model="applyValue" style="width:300px;padding: 0" size="large" v-if="status ===0">
                <Option v-for="item in applyList" :value="item.value" :key="item.value">{{item.label}}</Option>
              </Select>
              <div class="info">
                <span style="font-size: 0.14rem">在{{dateStart}}至{{dateEnd}}之间，{{applyValue}}涨价{{numberValue}}%</span>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
    <footer>
      <a href="javaScript:void(0)" @click="cancel">
        < 取消</a>
          <a href="javaScript:void(0)" @click="submit" v-if="!load">保存</a>
          <a href="javaScript:void(0)" v-if="load">保存 <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
    </footer>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        load: false,
        //提示信息
        content_show: "1",
        goodsType: "", // 名称
        numberValue: "", // 大约数值
        //        错误信息提示
        msgError: {
          startDateMsg: "",
          goodsName: "",
          endDateMsg: "",
          numberMsg: "",
          ruleMsg: "",
          applyMsg: "",
        },
        options: {
          disabledDate(date) {
            return date && date.valueOf() < Date.now() - 86400000;
          }
        },
        rulesValue: "", // 规则类型值
        ruleList: [{
            value: "请选择规则类型",
            label: "请选择规则类型"
          },
          {
            value: "1",
            label: "按比例涨价"
          },
          {
            value: "2",
            label: "固定金额涨价"
          }
        ],
        applyValue: "", //适用于  值
        applyList: [{
          value: "所有房型",
          label: "所有房型"
        }],
        dateStart: "", // 日期 开始
        dateEnd: "",
      }
    },
    props: ['id', "ruleLists", "status"],
    computed: {},
    methods: {
      ruleChange(value) {
        value !== '请选择规则类型' ? this.msgError.ruleMsg = '' : 0
      },
      numberFocus() {
        if(!this.rulesValue || this.rulesValue === '请选择规则类型') {
          this.msgError.ruleMsg = '请选择有效的规则类型'
        }
      },
      // 结束日期
      endOpen(value) {
        value ? this.msgError.endDateMsg = '' : (this.dateEnd ? 0 : this.msgError.endDateMsg = '请输入有效的结束时间')
      },
      //  开始日期 类似 于   blur  和input
      startOpen(value) {
        value ? this.msgError.startDateMsg = "" : (this.dateStart ? 0 : (this.msgError.startDateMsg = "请输入有效的开始时间"))
      },
      dateStartClick(value) {
        this.dateStart = value
      },
      dateEndClick(value) {
        this.dateEnd = value
      },
      deletePhoto(key) {
        this.$store.commit('deleteImgLists', key);
      },
      addSort() {
        this.$router.push('serveSorts')
      },
      //      输入设置
      input(value) {
        this.$refs[value].style.borderColor = 'skyblue';
        this.msgError[value] = ''
      },
      //      检测
      change(value, error) {
        if(value) {
          this.$refs[error].style.borderColor = 'gainsboro';
          this.msgError[error] = ''
        } else {
          this.$refs[error].style.borderColor = '#e64751';
          let msgText = "";
          switch(error) {
            case "goodsName":
              msgText = '请填写正确名称';
              break;
            case 'numberMsg':
              msgText = '请输入有效数字';
              break;
            default:
              break;
          }
          this.msgError[error] = msgText
        }
      },
      //   删除服务
      cancel() {
        this.$parent.shown = true;
        this.$parent.show = false;
        this.$parent.changeShow = false;
      },

      //    添加服务
      submit() {
        this.load = true;
        this.post("custom/Hotel/rules_add", {
          stores_id: this.id,
          name: this.goodsType,
          start_time: Math.ceil(new Date(this.dateStart).getTime() / 1000),
          over_time: Math.ceil(new Date(this.dateEnd).getTime() / 1000),
          is_restday: this.content_show,
          rules: this.rulesValue,
          rules_detail: this.numberValue,
          rules_range: this.applyValue
        }).then(res => {
          this.load = false;
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "设置价格",
              content: res.data.msg
            });
            this.cancel();
          } else {
            this.$Modal.error({
              title: "设置失败",
              content: res.data.msg
            });
          }
        });
      },
      getRoomList(id) {
        this.post("custom/Hotel/get_room_type", {
          stores_id: this.id
        }).then(res => {
          console.log(res.data.data);
          if(res.data.code === 10000) {
            if(res.data.data.length === 0) {
              return false;
            } else {
              let arr = [];
              res.data.data.forEach(item => {
                let obj = {};
                obj["label"] = item;
                obj["value"] = item;
                arr.push(obj);
              });
              this.applyList = this.applyList.concat(arr);
            }
          }
        })
      }
    },
    mounted() {
      if(this.status) {
        //                 修改
        this.post("custom/Hotel/rules_get", {
          id: this.status
        }).then(res => {
          if(res.data.code === 10000) {
            let dateStart = new Date(res.data.data.start_time * 1000);
            let dateEnd = new Date(res.data.data.over_time * 1000);
            this.goodsType = res.data.data.name;
            this.dateStart = dateStart.getFullYear() + "-" + (dateStart.getMonth() - 0 + 1) + "-" + dateStart.getDate();
            this.content_show = `${res.data.data.is_restday}`;
            this.dateEnd = dateEnd.getFullYear() + "-" + (dateEnd.getMonth() - 0 + 1) + "-" + dateEnd.getDate();
            this.numberValue = res.data.data.rules_detail;
            this.applyValue = res.data.data.rules_range;
          }
        })
      } else {
        //      添加
        this.getRoomList(this.id);
      }
    },
    filters: {
      imgFilters(value) {
        return value ? `../../static/image/${value}` : '../../static/image/noImg.png'
      }
    }
  }
</script>
<style scoped>
  .panel-title {
    border-bottom: 1px solid #ebecee;
    color: #636972;
    font-size: 20px;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
  
  h1 {
    font-size: 0.16rem;
    font-weight: 600
  }
  
  .info {
    padding: 0.05rem 0;
    margin: 0.2rem 0;
    color: #636972
  }
  
  .right {
    margin-top: 0.2rem;
  }
  
  .right .rightItem {
    display: flex;
    margin-top: 20px;
  }
  
  .rightItem .priceTip {
    color: #747981;
    font-size: 12px;
    margin-top: 8px;
  }
  
  .right .rightItem .item_item {
    margin-right: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .rightItem .item_item .item-title {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    min-height: 42px;
  }
  
  .item_item .item-title .tipMsg {
    color: #e64751;
  }
  
  .item_item .input-box {
    position: relative;
    ;
  }
  
  .right strong {
    display: inline-block;
    font-size: 15px;
  }
  
  .right input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 7px;
    color: #495060;
    font-size: 14px;
  }
  /*.right input::placeholder{*/
  /*font-size: 14px;*/
  /*}*/
  
  .right .text {
    width: 500px;
    padding: 0.1rem 0.1rem;
    height: 150px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    display: inline-block;
  }
  /*.left>div{height: 30vh;}*/
  
  .price p {
    padding: 0.1rem 0
  }
  
  .left input {
    display: inline-block;
    width: 300px;
    border-radius: 0.05rem;
    border: 1px solid gainsboro;
    padding: 0.1rem 0.1rem;
  }
  
  footer {
    position: fixed;
    bottom: 0;
    padding: 0.2rem 0.3rem;
    margin-left: -0.2rem;
    background: #fff;
    box-shadow: 0 -2px 3px #ddd;
    box-sizing: border-box;
    width: calc(100vw - 2.6rem);
    text-align: center;
  }
  
  footer>a {
    color: black;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    background: #eee;
    border-radius: 0.05rem;
  }
  
  footer>a:nth-child(1) {
    float: left;
  }
  
  footer>a:nth-child(2) {
    color: white;
    background: #93b719;
  }
  
  .info_slide {
    margin-top: 0.2rem;
    padding-left: 0.2rem;
    background: white;
    padding-bottom: 0.2rem;
    box-shadow: 5px 5px 10px #E7E7E7;
  }
</style>


