<template>
  <div class="goods">
    <div class="home-contend">
      <transition name="slide-left">
        <div v-if="shown" class="slideCon">
          <!--<header>
            <div class="header-one">
              <span>价格调整</span>
            </div>
            <p style="width: 100%">根据需求灵活调整价格，如节假日、周末等。</p>
          </header>-->
          <div class="selected-shop">
            <div class="selected-wrapper">
              <p class="text">选择门店</p>
              <div class="selected-item" v-for="(btnItem,index) in shopList">
                <span class="btn" @click="selectMeal(btnItem,index)">{{btnItem.stores_name}}</span>
                <div class="triangle" v-if="index === currentChoose">
                  <div class="triangle-inner"></div>
                </div>
              </div>
            </div>
          </div>
          <div class="addCenter" v-if="ruleList.length===0">
            <p><img src="../../assets/mingju.png"/></p>
            <p style="margin-bottom: 0.1rem;">根据需求制定灵活的价格策略。</p>
            <a href="JavaScript:void(0)" @click="add" class="icon">
              <i class="iconfont icon-iconjia"></i>
              <span>添加新规则</span>
            </a>
          </div>
          <div class="add_server" v-else>
            <div class="add-btn">
              <a href="JavaScript:void(0)" @click="add" class="icon">
                <i class="iconfont icon-iconjia"></i>
                <span>添加新规则</span>
              </a>
              <span style="float: right;margin-top: 0.2rem;color: #000011;font-size: 0.12rem;"><font style="color: red;">*</font>如多个规则相叠加，将以高价格为最优先策略。</span>
            </div>
            <div class="list-con">
              <div class="list-common list-title">
                <div class="name">名称</div>
                <div class="date">日期</div>
                <div class="rules">规则</div>
                <div class="apply">适用于</div>
                <div class="status">状态</div>
                <div class="delete"></div>
                <div class="arrow"></div>
              </div>
              
              <div class="con-list">
                <div class="list-common  listItem" v-for="(list,index) in ruleList" :key="index" @click="listItemClick(list.id)">
                  <div class="name">{{list.name}}</div>
                  <div class="date">{{list.start_time}} - {{list.over_time}}</div>
                  <div class="rules">{{list.rules}} ￥{{list.rules_detail}}</div>
                  <div class="apply">{{list.rules_range}}</div>
                  <div class="status">{{list.status}}</div>
                  <div class="arrow"><i class="iconfont icon-bianji"></i></div>
                  <div class="delete" @click.stop="close(list,index)"><i class="iconfont icon-lajitong"></i></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </transition>
      <div class="slideCon">
      <transition name="slide-right">
          <priceSlide v-if="show" @addRuleList="addRuleList" :id="mealId" :status="state" :ruleLists="ruleList"></priceSlide>
      </transition>
      </div>
    </div>
  </div>
</template>
<script>
  import priceSlide from './hotelPriceSlide.vue'
  export default {
    data() {
      return {
//       0 添加   其他 修改
        state:0,
        currentChoose:0,
        shopList:[],
        ruleList:[],// 规则列表
        type:0,
        Sshown:true,
        info: [],
        shown: true, // left 显示
        show: false, // right 显示
        id:"",// 当前 id 也就是每次点击列表项的时候 才会改变,
//        门店id
        mealId:''
      }
    },
    methods: {
      selectMeal(value,index){
        this.currentChoose = index ;
        this.mealId = value.id;
        this.getPriceList(value.id)
      },
      // 点击每个列表  获取详情
      listItemClick(id){
        this.state = id;
        this.show = true;
        this.shown = false;
      },
      //  模拟添加，后面可以去掉
      addRuleList(value){
        let length = this.ruleList.length;
        if(typeof value ==='object'){
          if(value.id !==''){
            for(let [index,e] of this.ruleList.entries()){
              if(value.id === e.id){
                this.ruleList.splice(index,1,value)
              }
          }
        }else{
            let lastArray = this.ruleList.slice(length-1)
            let newId = lastArray[0].id+1
            value.id = newId
            this.ruleList.push(value)
          }
      }},
      updateData(val){
        this.goodsLists=val;
      },
//        获取分类列表
      getSelectType(type){
        this.post('custom/Hotel/get_room_type', {
          page: 1,
          number: 15,
          stores_id:type
        }).then(res => {
          if(res.data.code === 10000) {
            this.goodsLists = res.data.data.info;
            this.goodsLists.forEach(item => {
              item.pic = "http://weapp.xiguawenhua.com/" + item.service_pic.split(',')[0];
              item["show"] = false
            });
            window.frames[0].postMessage(this.goodsLists,"*");
          }else{
            this.$Modal.error({
              title: "筛选失败",
              content: res.data.msg
            });
          }
        })
      },
//        筛选
      selectType(){
        this.getSelectType(this.type);
      },
//    鼠标滑过事件
      out(index) {
        this.goodsLists[index].show = false;
        this.goodsLists.splice(index, 1, this.goodsLists[index]);
      },
//    删除服务
      close(item,index) {
        this.$Modal.confirm({
          title: '删除服务',
          content: '<p>确认要删除该服务吗?</p>',
          onOk: () => {
            this.post("custom/Hotel/rules_delete", {
                id: item.id,
                stores_id:this.mealId
            }).then(res => {
              if(res.data.code === 10000) {
                this.ruleList.splice(index,1);
                const content = res.data.msg;
                this.$Modal.success({
                  title: "删除成功",
                  content: content
                });
              }else{
                const content = res.data.msg;
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            })
          },
        });
      },
//    鼠标移入事件
      enter(index) {
        this.goodsLists[index].show = true;
        this.goodsLists.splice(index, 1, this.goodsLists[index]);
      },
      add() {
        this.state = 0;
        this.show = true;
        this.shown = false;
      },
//      获取价格列表
      getPriceList(type){
          this.post("custom/Hotel/rules_list",{
            stores_id: type,
            page:1,
            limit:10
          }).then(res =>{
              if(res.data.code === 10000){
                this.ruleList = res.data.data;
                this.ruleList.forEach(item => {
                  if(item.rules === 1){
                    item.rules = "按比例涨价"
                  }else{
                    item.rules = "固定金额涨价"
                  }
                  item["status"] = "生效中";
                  let start_time = new Date(item["start_time"]*1000);
                  let end_time = new Date(item["over_time"]*1000);
                  item["start_time"] = start_time.getFullYear()+"-"+ (start_time.getMonth()-0+1) + "-"+ start_time.getDate();
                  item["over_time"] =end_time.getFullYear()+"-"+ (end_time.getMonth()-0+1) + "-"+ end_time.getDate()
                })
              }else{
                this.$Modal.error({
                  title: "获取信息失败",
                  content: res.data.msg
                });
              }
          })
      }
    },
    mounted() {

    },
    created() {
      this.post('custom/Hotel/list_stores', {}).then(res => {
        if(res.data.code === 10000) {
            this.shopList = res.data.data;
            this.mealId =  this.shopList[0].id;
            this.getPriceList(this.mealId);
        }
      })
    },
    activated(){

    },
    components: {
      priceSlide
    }
  }
</script>
<style scoped>
  .slide-left-enter-active,.slide-left-leave-active{
    transition: all 0.6s ease;
    opacity: 1;
    transform: translateX(0);
  }
  .slide-left-enter,.slide-left-leave{
    opacity: 0;
    transform: translateX(-100%);
  }
  .slide-right-enter-active,.slide-right-leave-active{
    transition: all 1s ease-in;
    opacity: 1;
    transform: translateX(0);
  }
  .slide-right-enter,.slide-right-leave{
    opacity: 0;
    transform: translateX(100%);
  }
  header {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
    display: inline-block!important;
  }
  header .header-one{display: flex;}
  header i{
    margin-right: 10px;
  }
  .header-one span {
    font-size: 19px
  }
  header>p{
    color: #636972;
    font-size: 14px;
    line-height: 1.4;
    margin-top: 5px;
  }
  .home-contend {
    height: calc(100vh - 78px - 92px);
    padding: 0.2rem 0;
    overflow-y: auto;
    position: relative;
  }
  .home-contend::-webkit-scrollbar{
    width:0.05rem;
    height:0.1rem;
  }
  .home-contend::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .home-contend .slideCon{
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
  /*.goods{
    background: white;
  }*/
    /* 滑块颜色 */
  .goods .home-contend .icon{
    display: inline-block;
    padding: 0.1rem 0.2rem;
    background: #59A2ED;
    color: white;
    font-size: 0.14rem;
    border-radius: 0.05rem
  }
  .addCenter{
    margin-top: 1.5rem;
  text-align: center;
  }
  .addCenter>p:nth-child(2){
    margin: 0.1rem 0 0.05rem 0;
    font-size: 0.14rem;
    /*font-weight: 800;*/
   color: #111111;
  }
 .add_server{
   margin-top: 20px;
   color: #636972;
 }
  .add_server .list-con{
    margin: 15px 0;
    font-size: 14px;
  }
  .add_server .list-con .list-title{
    color: #4E4E4E;
    font-weight: 600;
    text-align: center;
    padding: 10px 0;
    background: #F9FAFB;
    border-radius: 3px 0 0 3px;
    /*border: 1px solid #d8d9db;*/
  }
  .list-con .listItem{
    padding: 12px 0;
    border-bottom: 1px solid #eee;
    transition:  all 0.3s ease;
  }
  .list-con .listItem:hover{
    background-color: #eee;
  }
.list-common{
  width: 100%;
  display: flex;
  text-align: center;
}
  .add_server .add-tip{color: #a9aeb2;display: flex}
  .add_server .add-tip span{display: inline-block;margin-left: 5px;}
  .list-common .name{width: 11%;padding-left:1%;}
  .list-common .date{width: 25%}
  .list-common .rules{width:24% }
  .list-common .apply{width: 18%}
  .list-common .status{width: 10%}
  .list-common .delete{width: 6%}
  .list-common .arrow{width: 5%;}
  .slide-fade-enter-active {
    transition: all 1s ease;
  }

  .slide-fade-enter {
    transform: translateX(70%);
  }
/*选择门店*/
  .selected-shop{overflow-x: scroll;overflow-y: hidden;}
  .selected-shop::-webkit-scrollbar{
    width:5px;
    height:6px;
  }
  .selected-shop::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .selected-wrapper{align-items: center;border-bottom: 1px solid #ddd;display: flex;padding: 15px 0;white-space: nowrap;}
  .selected-wrapper .text{color: #888b94;font-size: 12px;margin: 0 10px;}
  .selected-wrapper .selected-item{margin-right: 10px;position: relative;}
  .selected-wrapper .selected-item .btn{background: #59A2ED; color: #FFFBFF;font-size: 12px;  padding: 8px 12px;
    margin: 3px; transition: all 0.15s;text-decoration: none;font-weight: bold;  -webkit-appearance: none;border-radius: 4px;cursor: pointer;
    text-transform: uppercase;min-width: 80px;display: inline-block;text-align: center;border: 1px solid #ccc;line-height: 1;}
  .selected-wrapper .selected-item .btn:hover{background-color: #eee;border-color: #ccc;}
  .triangle, .triangle-inner{
    border: 27px solid #fff;
    height: 0;
    width: 0;
  }
  .selected-wrapper .selected-item .triangle{
    border-color: transparent transparent #ddd;
    border-width: 8px;
    left: 50%;
    margin-left: -8px;
    position: absolute;
    top: 36px;
  }
  .triangle {
    border-color: transparent transparent transparent #ebecee;
    bottom: 0;
    margin-left: -8px;
    position: absolute;
    right: 45%;
  }
  .selected-wrapper .selected-item .triangle-inner{
    border-color: transparent transparent #fff;
    border-width: 8px;
    left: 50%;
    margin-left: -8px;
    position: absolute;
    top: -6px;
  }
</style>



